.castList {
  list-style: none;
  margin: 0;
  padding: 20px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 15px;
}

.castMember {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.57);
  border-radius: 2px;
  width: 180px;
}

.name {
  margin: 10px 0;
  color: #90cea1;
  font-size: 16px;
  font-weight: 500;
  padding-left: 5px;
}

.character {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 12px;
  padding-left: 5px;
  color: #01b4e4;
}

.characterName {
  margin-top: 0;
  margin-bottom: 14px;
  font-size: 16px;
  font-weight: 700;
  color: #0d253f;
  padding-left: 5px;
}
.empty {
  margin: 20px 0;
  color: #0f1e64;
  font-size: 22px;
  font-weight: 500;
}
